<template>
 <com-layout :layout_class="set.layout_class" :head_class="set.head_class" :main_class="set.main_class" :foot_class="set.foot_class">
    <template slot="head">
      <View-group v-for="(item,key) in set.head" :key="key" :set="item" :type="item.type" :data="item.data" />
    </template>
    <template slot="main">
        <com-form :data="set.form" :form="form" @getinfo="getinfo"/>
         <div class="d-grid"><button class=" btn btn-primary" @click="onsubmit"></button></div>
    </template>
    <template slot="foot">
      <View-group v-for="(item,key) in set.foot" :key="key" :set="item" :type="item.type" :data="item.data" />
    </template>
  </com-layout>
</template>

<script>
import Layout from './public-amap'
import ViewGroup from '../group/start'
import Iconspill from "../block/icons/pill-icons";
import ViewForm from "../form/p-edit-start";
export default {
  components: {
    Layout,
    ViewGroup,
    ViewForm,
    Iconspill,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
        return {
            form:{},
        }
  },
  created(){
    this.form={
      origin:this.$route.query.origin,
      origin_lng:this.$route.query.origin_lng,
      origin_lat:this.$route.query.origin_lat,
      destination:this.$route.query.destination,
      destination_lng:this.$route.query.destination_lng,
      destination_lat:this.$route.query.destination_lat,
      "carpooling": 0,
      "setdate": "",
      "people": "",
      "thankfee": "",
      "message": ""
    };
  },
  methods:{
      onvalidate(){
          for(let x in this.form){
            if(this.form[x]==="" || this.form[x].length===0){
              if(this.set.form[x]){
                if(this.set.form[x].validate){
                this.set.form[x].isActive=true
                return 0;
                }
              }
            }
          }
          return 1;
      },
      onsubmit(){
        if(this.onvalidate()===1){
         this.$emit('onsubmit',{method: 'post',url: '/driver/riding',params:{token:this.$store.state.token},data:this.form,});
        }
      },
      getinfo(key){
        this.onvalidate();
      },
  }
}
</script>



