<template>
  <div class="p-2" >
     <div  @click="$emit('getset',name,'a')"> {{form}}</div>
      {{data}}<br>
      <div @click="data.validate=true">{{data.selectdd}}</div>
        <input type="text" class="form-control" :placeholder="data.set.placeholder" v-model="form[name]" @keyup="aa"/>
        <div v-show="data.validate" @click="data.validate=false">

            111222
        </div>
    </div>  
</template>

<script>
export default {
  props: {
    name:"",
    data:{},
    form:{},
  }
  ,methods:{
      bb(key){
        this.form[this.name]=key; 
        this.aa()
      },
      aa(){
        this.$emit('getrefresh',this.name)
        this.$forceUpdate();
      },
  }
}
</script>

