<template>
   <Layout v-if="data.set" :type="data.type" :set="data.set" :data="data.data" :group="data.group" @refresh="ispathaxios"/>
</template>

<script>
import pathmethods from '@/router/axios-ajax'
import Layout from '../components/layout/start'
export default {
  components: {
    Layout,
  },
  props: {
    datacache:{},
  },
  data() {
    return {
      data:{},
      viewkey:'index',
      tips:[],
      loading:false,
    };
  },
  created () {
    this.ispathMatch();
  },
  watch:{
     $route(){
        this.ispathMatch();
     }
  },
  methods:pathmethods
};
</script>


