<template>
    <div class="p-2">
        <div class="row text-center" v-if="info.nav">
            <div class="col" v-if="info.nav.mobile">
                <div class="border rounded"><span class="bi bi-tablet-fill me-1"></span></div>
            </div>
            <div class="col" v-if="info.nav.cancel">
                <div class="border rounded"  @click="$emit('onsubmit',7)"><span class="bi bi-bookmark-x-fill me-1"></span></div>
            </div>
        </div>
        <div class="d-grid mt-2">
            <button v-if="info.state" class=" btn btn-primary" @click="$emit('onsubmit',info.state)">{{info.name}}</button>
            <button v-else class=" btn btn-dark" disabled>{{info.name}}</button>
        </div>
    </div>
</template>

<script>
export default {
  props: {
    set:{},
    info:{},
    show:{},
  },
}
</script>

<style>

</style>


