<template>
  <div>
    <div class="d-flex justify-content-between align-items-center p-2 border border-bottom border-light"  @click="data.isActive=true">
        <div class="bd-highlight fs-6">
            {{data.name}}
        </div>
        <div class="bd-highlight">
            <img :src="$store.state.imgurl+form[name]" width="50" height="50" class="rounded-pill border border-2 border-white">
            <span class="bi bi-chevron-right"></span>
        </div>
    </div>
    <LayoutFrom :title="data.title" :text="data.text" :isActive="data.isActive" @isActive="onActive">
        <div class="container-fluid">
        <div class="row row-cols-4 g-2">
            <div class="col text-center" v-for="(item,key) in data.selectdd" :key="key" @click="selected=item">
                <img :src="$store.state.imgurl+item" width="60" height="60" class="rounded-pill border border-3" :class="{'border-info':item===selected,'border-white':item!==selected}">
            </div>
        </div>
        </div>
        <div class="d-grid p-2">
            <button class=" btn btn-primary" @click="onsubmit"></button>
        </div>
    </LayoutFrom>
   </div>  
</template>

<script>
import LayoutFrom from './layout-bottom';
export default {
  components: { 
    LayoutFrom,
  },
  props: {
    name:"",
    data:{},
    form:{},
  },
  data(){
      return {
          selected:'',
      }
  },
  created(){
     this.$set(this,"selected",this.form[this.name]);
  },
  methods:{
      onActive(){
        this.data.isActive=false;
      },    
      onsubmit(){
        this.form[this.name]=this.selected
        this.$emit('getrefresh',this.name)
        this.$emit('getform',this.name,this.form[this.name]);
        this.onActive();
      },
  }
}
</script>

<style>

</style>


