<template>
   <div class="position-fixed top-0 start-0 end-0 bottom-0 d-flex align-items-center justify-content-center text-center">
    <div class="position-fixed top-0 start-0"><span class="fs-5 bi bi-chevron-left m-2"  @click="$tourl('../')"></span></div>
    <div class="bd-highlight">
      <h1 class="fw-light">{{set.title}}</h1>
      <p class="lead text-muted">{{set.text}}</p>
      <com-form :data="set.form" :form="form"/>
      <div class="d-grid p-2 m-2">
            <button class=" btn btn-primary" @click="onsubmit"></button>
      </div>
      <p class="lead text-muted"></p>
    </div>
  </div>
</template>


<script>
export default {
  components: {
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
    return {
      form:{},
    }
  },
  created(){
    this.form=this.data;
  },
  methods:{
    onvalidate(){
        for(let x in this.form){
          if(this.form[x]==="" || this.form[x].length===0){
            if(typeof this.set.form[x]!=='undefined'){
              if(typeof this.set.form[x].validate!=='undefined'){
              this.set.form[x].validate=true
              return 0;
              }
            }
          }
        }
        return 1;
    },
    onsubmit(){
      if(this.onvalidate()===1){
      this.$emit('onsubmit',{method: 'get',url:this.set.formurl,params:this.form});
      }
    }
  }
}
</script>

<style>

</style>


