<template>
    <div class="pb-2" v-if="info">
      <div class="fs-6 text-center" v-if="show.ispay===1 && info.pay">{{info.pay}}</div>
      <div class="fs-6 text-center" v-if="show.ispay===0 && info.nopay">{{info.nopay}}</div>
      <div class="fs-6 text-center" v-if="info.title">{{info.title}}</div>
      <div class="text-black-50 fs-sm text-center" v-if="info.text">{{info.text}}</div>
      <div class="fs-6 text-center" v-if="info.ltitle">{{info.ltitle}}</div>
      <div class="text-black-50 fs-sm text-center" v-if="info.ltext">{{info.ltext}}</div>
    </div>
</template>

<script>
export default {
  props: {
    set:{},
    info:{},
    show:{},
  },
}
</script>

<style>

</style>


