<template>
    <div class="bg-light">
        <div class="position-fixed top-0 start-0 end-0 bottom-0">
            <div id="container" class="h-100"></div>
        </div>
        <div class="position-fixed top-0 start-0">
        <span @click="$tourl('../')"  class="fs-4 bi bi-chevron-left m-2"></span>
        </div>
        <div class="bg-white position-fixed start-0 end-0 bottom-0 rounded rounded-30">
            <div class="text-center" @click="isshow=isshow===false?true:false"><span class="bi fs-2 text-black-50" :class="isshow===false?'bi-chevron-compact-up':'bi-chevron-compact-down'"></span></div>
            <Trip-state :set="set" :info="set.stateinfo[data.show.state]"  :show="data.show"/>
            <div class="p-2">
                <User-info :set="data.show"/>
            </div>
            <div class="p-2" v-if="isshow">
                <Trip-info :set="data.show"/>
            </div>
             <Trip-submit :set="set" :info="set.stateinfo[data.show.state].submit"  :show="data.show" @onsubmit="onsubmit"/>
        </div>
    </div>
</template>

<script>
import Layout from '../public-amap'
import UserInfo from "./user-info";
import TripInfo from "./trip-info";
import TripState from "./trip-state";
import TripSubmit from "./trip-submit";
export default {
  components: {
    Layout,
    UserInfo,
    TripInfo,
    TripState,
    TripSubmit,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
    return {
      isshow:true,
    }
  },
  mounted(){
    ysdt._this=this;
    if(this.data.show.origin && this.data.show.destination){
      ysdt.showdriving([this.data.show.origin_lng,this.data.show.origin_lat],[this.data.show.destination_lng,this.data.show.destination_lat]);
    }
  },
  methods:{
      onsubmit(state){
         this.$emit('onsubmit',{method: 'post',url:this.set.submiturl,params:{token:this.$store.state.token},data:{id:this.data.show.id,state:state}});
      },
  }
}
</script>



