<template>
  <div class="bg-white rounded p-3 fs-normal position-relative">
      <div class="d-flex justify-content-between align-items-center">
          <div class="bd-highlight stretched-link" @click="$emit('geturl','id',data.id)">
              <span class="fs-6 text-primary">68%</span>   14:30
          </div>
          <div class="bd-highlight">
              <span class="fs-6 fw-bold">15</span>
          </div>
      </div>
      <div class="d-flex justify-content-between py-2">
          <div class="bd-highlight d-flex flex-row">
                <div class="bd-highlight me-2">
                    <Userlogo :icons="'person-fill'"/>
                </div>
                <div class="bd-highlight">
                    <div class="fw-bold"> </div>
                    <div class="fs-sm">5.0 - <span class="text-primary">1</span></div>
                </div>
          </div>
          <div class="bd-highlight">
               <div class="btn btn-white border-primary btn-sm"></div>
          </div>
      </div>
    
  </div>
</template>

<script>
import Userlogo from "../icons/user-logo";
export default {
  components: {
    Userlogo,
  },
  props: {
    set:{},
    data:{},
  },
}
</script>



