<template>
    <div class="d-flex justify-content-between align-items-end">
        <div class="bd-highlight">
            <div>{{$datename(set.eldate)}}</div>
            <div class="ymerge">{{set.origin}}</div>
            <div class="ymerge">{{set.destination}}</div>
        </div>
        <div>
            <div>
                <span class="fs-4">{{set.price}}</span><span class="bi bi-chevron-right"></span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  components: {
  },
  props: {
    set:{},
  },
}
</script>

<style>

</style>


