            </div>
            <div class="p-3">
                <div class="d-flex justify-content-between align-items-end">
                    <div class="bd-highlight">
                        <div @click="onsearch('name','', '')">d</div>
                        <div @click="onsearch('name','', '')">d</div>
                        <div @click="onsearch('name','', '')">d</div>
                    </div>
                    <div>
                        <div>fdsf</div>
                    </div>
                </div>
            </div>
        </template>
    </Layout>
</template>

<script>
import Layout from './public-amap'
import ViewGroup from '../group/start'
export default {
  components: {
    Layout,
    ViewGroup,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
        return {
            form:{},
        }
  },
  created(){
    this.form=this.data;
  },
  mounted(){
  
  },
  methods:{
    
  }
}
</script>

