<template>
    <Common :datacache="datacache"/>
</template>

<script>
import indexJson from '@/json/driver/index.json';
import ridingJson from '@/json/driver/riding.json';
import loginJson from '@/json/login.json';
import userindex from '@/json/user-index.json';
import prorderShow from '@/json/driver/prorder-show.json';

import Common from "./common";
import driverSearch from '@/json/driver-search.json';
import driverEdit from '@/json/driver-edit.json';
export default {
  //  channel
  components: {
    Common,  
  },
  data() {
    return {
      datacache:{
        index:indexJson,
        driver:indexJson,
        riding:ridingJson,
        login:loginJson,
        user:userindex,
        "driver_search":driverSearch,
        "driver_edit":driverEdit,
        "user-order":userindex,
        "user-info":userindex,
        prorder_show:prorderShow,
        },
    };
  },
   
};

</script>


