// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios)
import App from './App'
import store from './store'
import router from './router'

//bootstrap
import "./assets/css/bootstrap.min.css"
import "./assets/css/bootstrap-icons.css"
import "./assets/js/bootstrap.bundle.min.js"
//ys
import "./assets/css/ys.css"
import "./assets/css/anim.css"

import "./router/ysdt.js"

import toastRegistry from './toast/index'
Vue.use(toastRegistry)
Vue.config.productionTip = false

import comLayout from '@/ysview/layout'
Vue.component('com-layout', comLayout)
import comForm from '@/components/form/start'
Vue.component('com-form', comForm)
import comTrip from '@/components/trip/start'
Vue.component('com-trip', comTrip)
    /* eslint-disable no-new */
new Vue({
    el: '#app',
    store,
    router,
    components: { App },
    template: '<App/>'
})


