<template>
    <View-trip :set="setdata[set.binding]" :group="group" :data="data" @onsubmit="onsubmit"/>
</template>

<script>
import ViewTrip from "./trip/start";
import setdata from './trip/setdata.json';
export default {
  components: {
    ViewTrip,
  },
  props: {
    set:{},
    group:'',
    data:{},
  },
  data(){
    return{
      setdata,
    }
  },
  methods:{
      onsubmit(key){
         this.$emit('onsubmit',key);
      },
  }
}
</script>



