Date.prototype.format = function(format) {
    var args = {
        "M+": this.getMonth() + 1,
        "d+": this.getDate(),
        "h+": this.getHours(),
        "m+": this.getMinutes(),
        "s+": this.getSeconds(),
        "q+": Math.floor((this.getMonth() + 3) / 3), //quarter
        "S": this.getMilliseconds()
    };
    if (/(y+)/.test(format)) format = format.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var i in args) { var n = args[i]; if (new RegExp("(" + i + ")").test(format)) format = format.replace(RegExp.$1, RegExp.$1.length == 1 ? n : ("00" + n).substr(("" + n).length)); }
    return format;
};

function formatname(key) {
    if (key) {
        key = key.replace(new Date(new Date().getTime() + 0 * 60 * 60 * 1000).format("yyyy-MM-dd"), '')
        key = key.replace(new Date(new Date().getTime() + 24 * 60 * 60 * 1000).format("yyyy-MM-dd"), '')
        key = key.replace(new Date(new Date().getTime() + 48 * 60 * 60 * 1000).format("yyyy-MM-dd"), '')
    }
    return key;
}
export function datardf(key) {
    var myDate = key ? new Date(key) : new Date();
    var _mh = myDate.getHours();
    var _mm = myDate.getMinutes();
    _mm = parseInt(_mm / 5) + 2;
    if (_mm >= 11) {
        _mh = _mh + 1;
        _mm = _mm - 11;
    }
    let _mi = 0;
    if (_mh >= 24) {
        _mi = 1;
    }
    var _data = [];
    let _iagm = key ? new Date(key).getHours() === 23 ? 2 : 1 : 3;
    for (let i = _mi; i < _iagm; i++) {
        /**/
        var obj = {};
        obj.value = new Date(myDate.getTime() + (i * 24) * 60 * 60 * 1000).format("yyyy-MM-dd");
        obj.label = formatname(new Date(myDate.getTime() + (i * 24) * 60 * 60 * 1000).format("yyyy-MM-dd"));
        // $datename
        var _data2 = [];
        for (let n = 0; n < 24; n++) {
            /**/
            if (i != 0 || n >= _mh) {
                var obj2 = {};
                if (n < 10) {
                    obj2.label = '0' + (n) + '\u70b9';
                    obj2.value = '0' + (n);
                } else {
                    obj2.label = (n) + '\u70b9';
                    obj2.value = (n);
                }
                var _data3 = [];
                for (let y = 0; y < 12; y++) {
                    /**/
                    if (n != _mh || y >= _mm) {
                        var obj3 = {};
                        if (y == 0 || y == 1) {
                            obj3.label = '0' + (y * 5) + '\u5206';
                            obj3.value = '0' + (y * 5);
                        } else {
                            obj3.label = (y * 5) + '\u5206';
                            obj3.value = (y * 5);
                        }
                        _data3.push(obj3)
                    }
                }
                obj2.children = _data3;
                _data2.push(obj2);
            }
        }
        obj.children = _data2;
        _data.push(obj)
    }
    return _data;
}


