<template>
    <Common :datacache="datacache"/>
</template>

<script>
import indexJson from '@/json/passenger/index.json';
import passengerEdit from '@/json/passenger-edit.json';
import passengerSearch from '@/json/passenger-search.json';
import ridingJson from '@/json/passenger/riding.json';
import loginJson from '@/json/login.json';
import userindex from '@/json/user-index.json';

import Common from "./common";
export default {
  //  channel
  components: {
    Common,  
  },
  data() {
    return {
      datacache:{
        index:indexJson,
        passenger:indexJson,
        "passenger_edit":passengerEdit,
        "passenger_search":passengerSearch,
        riding:ridingJson,
        login:loginJson,
        user:userindex,
        "user-order":userindex,
        "user-info":userindex,
        },
    };
  },
   
};

</script>


